"use client";

import * as React from "react";
import Image from "next/image";
import Link from "next/link";
import { usePathname } from "next/navigation";

import { cn } from "@/lib/utils";
import {
  NavigationMenu,
  NavigationMenuContent,
  NavigationMenuItem,
  NavigationMenuLink,
  NavigationMenuList,
  NavigationMenuTrigger,
  navigationMenuTriggerStyle,
} from "@/components/ui/navigation-menu";
import { buttonVariants } from "../ui/button";
import { AlignRight } from "lucide-react";
import { Button } from "@/components/ui/button";

import {
  Sheet,
  SheetClose,
  SheetContent,
  SheetDescription,
  SheetFooter,
  SheetHeader,
  SheetTitle,
  SheetTrigger,
} from "@/components/ui/sheet";

const navData: { title: string; href: string }[] = [
  {
    title: "Home",
    href: "/",
  },
  {
    title: "Courses",
    href: "/courses",
  },
  {
    title: "About Us",
    href: "/about",
  },
  {
    title: "Pricing",
    href: "pricing",
  },
  {
    title: "Contact",
    href: "/contact",
  },
];

function NavBar() {
  const path = usePathname();

  return (
    <nav className="flex h-[73px] items-center justify-between px-0 py-4 md:h-[81px] md:px-[60px] md:pb-5 md:pt-4 lg:h-[99px] lg:px-[132px] lg:pb-6 lg:pt-5">
      <div className="flex items-center md:h-[45px] md:gap-[5px] lg:h-[55px] lg:gap-[50px]">
        <div className="flex items-center justify-center rounded-lg bg-primary sm:h-10 md:size-[45px] lg:size-[54px]">
          <Link href={"/"}>
            <Image
              src={"/assets/shape-17.png"}
              alt="logo"
              width={32.5}
              height={32.5}
              className="xs:h-7 object-fill sm:w-7"
            />
          </Link>
        </div>
        <div className="hidden items-center md:gap-4 lg:flex lg:gap-[26px]">
          <NavigationMenu>
            <NavigationMenuList>
              <NavigationMenuItem>
                {navData.map((item) => (
                  <Link
                    key={item.href}
                    href={item.href}
                    legacyBehavior
                    passHref
                  >
                    <NavigationMenuLink
                      className={cn(
                        navigationMenuTriggerStyle(),
                        "w-auto md:h-[45px] md:px-5 md:py-3 md:text-sm lg:h-[55px] lg:px-6 lg:py-[14px] lg:text-lg",
                      )}
                      active={path.includes(item.href)}
                    >
                      {item.title}
                    </NavigationMenuLink>
                  </Link>
                ))}
              </NavigationMenuItem>
            </NavigationMenuList>
          </NavigationMenu>
        </div>
      </div>

      <div className="flex h-[45px] items-center gap-5 lg:h-[55px]">
        <Link
          href="/signup"
          className={cn(
            buttonVariants({
              variant: `${path.includes("/signup") ? "default" : "ghost"}`,
            }),
            "h-[45px] w-[75px] px-6 md:w-auto md:py-3 md:text-sm lg:h-[55px] lg:py-[14px] lg:text-lg",
          )}
        >
          Sign Up
        </Link>

        <Link
          href="/login"
          className={cn(
            buttonVariants({
              variant: `${
                path.includes("/login") || !path.includes("/signup")
                  ? "default"
                  : "ghost"
              }`,
            }),
            "h-[45px] w-auto px-6 md:py-3 md:text-sm lg:h-[55px] lg:py-[14px] lg:text-lg",
          )}
        >
          Login
        </Link>
        <AlignRight className="block lg:hidden" size={ 34 } />
        
      </div>
    </nav>
  );
}

export default NavBar;
